import {useState} from "react";
import {Link} from "react-router-dom";
import {
    Box,
    Button,
    Center,
    Flex,
    Grid,
    GridItem,
    IconButton,
    Image,
    Menu,
    MenuButton,
    MenuItem,
    MenuList, Text
} from "@chakra-ui/react";
import {HamburgerIcon} from "@chakra-ui/icons";

import logo from '../logo.jpg';
import "./navBar.css"

const tabs = [
    {
        "name" : "Danh mc",
        "path" : "/"
    },{
        "name" : "Bi vit",
        "path" : "/comingSoon"
    },{
        "name" : "Gii thiu",
        "path" : "/about"
    },{
        "name" : "Lin h",
        "path" : "/contact"
    }
]

function NavBar(){

    const [state, setState] = useState("/");

    return(
        <Box bg='#ffde46' w='100%' color='#5f5338' fontWeight='bold'>
               <Flex margin='auto' maxW='1050px'>
                   <Box w='20%'>
                       <Image
                           p='0.5'
                           margin='auto'
                           boxSize='70px'
                           objectFit='cover'
                           src={logo}
                           alt='Hip Ho Farm'
                       />
                   </Box>
                   <Box w='80%' margin='auto'
                        display={{ base: 'none', md:'block'}}
                   >
                       <Grid templateColumns='repeat(4, 1fr)' gap={2}>
                           {tabs.map(tab => {
                               return(
                                   <GridItem w='100%' margin='auto' key={tab.path}>
                                       <Link to={tab.path}>
                                           <Center h='9' textAlign='center'
                                                   onClick={() => setState(tab.path)}
                                                   className={state===tab.path?('navActive'):('navItem')}
                                           >
                                               {tab.name}
                                           </Center>
                                       </Link>
                                   </GridItem>
                               )
                           })}
                       </Grid>
                   </Box>
                   <Box w='80%' margin='auto'
                        display={{ base: 'block', md:'none'}}
                   >
                       <Flex>
                           <Box w='80%' p={1} textDecoration='underline overline'>
                               <Text fontSize={{ base: 'sm', sm:'lg'}}
                               >MANG THC PHM SCH</Text>
                               <Text fontSize={{ base: 'sm', sm:'lg'}}
                                     textAlign='right'
                               >N NGI NH CA BN</Text>
                           </Box>
                           <Box backgroundColor='#eef0c6' margin='auto' borderRadius='10'>
                               <Menu>
                                   <MenuButton
                                       as={IconButton}
                                       aria-label='Options'
                                       icon={<HamburgerIcon />}
                                       variant='outline'
                                   />
                                   <MenuList background="#e8e258">
                                       {tabs.map(tab => {
                                           return(
                                               <Link to={tab.path} key={tab.path}>
                                                   <MenuItem >{tab.name}</MenuItem>
                                               </Link>
                                           )
                                       })}
                                   </MenuList>
                               </Menu>
                           </Box>
                       </Flex>
                   </Box>
               </Flex>

        </Box>
    );
}
export default NavBar;